<template>
    <!--begin::Modal-->
    <div class="modal fade" id="formModal" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered" role="document">
            <form @submit.prevent="store" class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="formModalLabel">إرسال رسالة</h5>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close">
                        </button>
                </div>
                <div class="modal-body row">
                    <div class="col-12 mt-3">
                        <label>التليفون</label>
                        <input v-model="form.phone" type="text" autocomplete="off" class="form-control" :class="{ 'is-invalid': form.errors.has('phone') }">
                        <has-error :form="form" field="phone"></has-error>
                    </div>
                    <div class="col-12 mt-3">
                        <label>الرسالة</label>
                        <textarea v-model="form.message" rows="2" dir="rtl" cols="50" type="text" 
                            autocomplete="off" class="form-control" :class="{ 'is-invalid': form.errors.has('message') }"></textarea>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-light" data-bs-dismiss="modal">إلغاء</button>
                    <button type="submit" class="btn btn-primary">إرسال</button>
                </div>
            </form>
        </div>
    </div>
    <!--end::Modal-->
</template>
<script>
import { Form, HasError } from 'vform'
import axios from 'axios';
import "vue-select/dist/vue-select.css";
export default {
    components: { HasError },
    props: ['fetchData'],
    data() {
        return {
            form: new Form({
                phone: null,
                message: null,
            }),
        };
    },
    methods: {
        create() {
            this.form.reset();
            $('#formModal').modal('show');
        },
        store() {
            this.form.post("/api/messages").then(({ data }) => {
                if (data.success!=0) {
                    let text='Error In Send Message';
                    switch(data.success) { 
                        case -1: {text="Invalid username or password";break;} 
                        case -2: {text="SMS has been sent by an account through not white listed IPs of the sender's account";break;} 
                        case -3: {text="SMS sent to black listed dial of the sender's account";break;} 
                        case -5: {text="Account's quota has ended";break;} 
                        case -6: {text="SMS platform database down";break;} 
                        case -7: {text="The sending account isn't active";break;} 
                        case -11: {text="The account exceeded the expire date";break;} 
                        case -12: {text="There is no text";break;} 
                        case -13: {text="There is a problem with the connection";break;} 
                        case -14: {text="While sending SMS an error occurs and SMS not sent";break;} 
                        case -100: {text="Internal server error";break;} 
                        case -16: {text="This account doesn't have the send with DLR option.";break;} 
                        case -18: {text="ANI is wrong";break;} 
                        case -19: {text="Repeated SMS Id, and SMS ID must be unique";break;} 
                        case -21: {text="The sending account is not exist in the system";break;} 
                        case -22: {text="SMS not validate yet because the database is down, it will be validated later and send to kannel";break;} 
                        case -23: {text="The connection is wrong";break;} 
                        case -26: {text="SMS ID not a GUID";break;} 
                        case -29: {text="User name or password fields has no data";break;} 
                        case -30: {text="Invalid SMS fake sender";break;} 
                        case -31: {text="The start time is invalid";break;} 
                        case -32: {text="In case of the client enters a key not included in the “Items” parameter";break;} 
                        case -1: {text="DeliveredToPhone";break;} 
                    } 
                    this.$swal.fire({ icon: 'error', title: "Error", text: text + '!' });
                } else {
                    this.$swal.fire({ icon: 'success', title: "Done", text: 'تم إرسال الرسالة' });
                    this.fetchData();
                    $('#formModal').modal('hide');
                }
            })
        },
    },
}
</script>
