<template>
    <layout-default>
        <div class="page-content">
            <div class="container-fluid">
                <!-- start page title -->
                <div class="row">
                    <div class="col-12">
                        <div class="page-title-box d-flex align-items-center justify-content-between">
                            <h4 class="mb-0">جدول الرسائل</h4>
                            <div class="page-title-right">
                                <button type="button" @click="$refs.FormModal.create()" class="btn btn-success waves-effect waves-light">
                                        + إرسال رسالة
                                    </button>
                                <form-modal ref="FormModal" :fetchData="fetchData" />
                            </div>
                        </div>
                    </div>
                </div>
                <!-- end page title -->
                <div class="row">
                    <div class="col-12">
                        <div class="card">
                            <div v-if="messages&&messages.data.length" class="card-body">
                                <table id="datatable" class="table table-bordered dt-responsive nowrap" style="border-collapse: collapse; border-spacing: 0; width: 100%;">
                                    <thead>
                                        <tr>
                                            <th>المرسل</th>
                                            <th>التليفون</th>
                                            <th>التاريخ</th>
                                            <th>الرسالة</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(message,index) in messages.data">
                                            <td>{{(message.user)?message.user.name:'SYSTEM'}}</td>
                                            <td>{{message.phone}}</td>
                                            <td>{{message.created_at| moment("DD/MM/YYYY")}}</td>
                                            <td>{{message.message}}</td>
                                        </tr>
                                    </tbody>
                                </table>
                                <pagination-nav v-if="messages.last_page>1" :pageSize="5" :currentPage="2" :GoToPage="GoToPage" />
                            </div>
                            <div v-else class="card-body">
                                <beat-loader v-if="loader" :loading="loader" color="#5578eb"></beat-loader>
                                <p v-else class="card-title-desc" style="text-align: center;">
                                    لا يوجد رسائل حتى الآن
                                </p>
                            </div>
                        </div>
                    </div>
                    <!-- end col -->
                </div>
                <!-- end row -->
            </div>
            <!-- container-fluid -->
        </div>
    </layout-default>
</template>
<script>
import LayoutDefault from '../../layouts/App';
import PaginationNav from '../../components/pagination';
import FormModal from '../../components/modals/message/add';
import axios from 'axios';
export default {
    components: { LayoutDefault, PaginationNav, FormModal },
    data() {
        return { messages: null, loader: false }
    },
    created() {
        this.fetchData();
    },
    methods: {
        fetchData(num = 1) {
            this.messages = null;
            this.loader = true;
            let data = { params: { page: num } };
            axios.get('/api/messages', data).then(({ data }) => {
                this.messages = data.success.messages;
                if (!data.success.messages.data.length && num != 1) {
                    this.fetchData(1)
                }
                this.loader = false;
            });
        },
    },
}
</script>